'use strict';
import Pageable from 'module-crud-ui/app/scripts/crud.pageable.js';
import Saveable from 'module-crud-ui/app/scripts/crud.saveable.js';
/* global angular */

export default class MaterialProgrammingCtrl extends Pageable {

  /*@ngInject*/
  constructor(
    $injector,
    $state,
    $filter,
    $window){
    super(
      {
        injector: $injector,
        endpoint: 'materialmedias',
        params:{
          fromChapter:$state.params.chapterId,
          toChapter:$state.params.chapterId,
          materialId:$state.params.materialId
        },
        selectable:
        {
          itemKey: 'cid',
          enabled: true,
          allowMultiSelect: false,
          params: {}
        },
        executeGet:true
      }
    );
    this.programmingPageable = new Pageable(
      {
        injector: $injector,
        endpoint: 'dailyProgramming',
        params:{
          chapterId:$state.params.chapterId,
          materialId:$state.params.materialId
        },
        selectable:
        {
          itemKey: 'id',
          enabled: true,
          allowMultiSelect: false
        },
        executeGet: true
      }
    );
    this.state=$state;
    this.filter=$filter;
    this.window = $window;
    this.actions = [
      {
        enabled:true,
        tooltip: this.filter('translate')('tooltip.search'),
        action: () => { this.goToSearchResult(); },
        icon: 'arrow-left'
      }];
  }

  _load(){
    let params = {
      materialId: this.state.params.id,
      fromChapter: this.state.params.chapterId,
      toChapter: this.state.params.chapterId,
    };
    this.get(params);
  }

  goToSearchResult(){
    this.state.go('searchResults', this.state.params);
  }

}
